

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn-outline-light {
  border-color: #c934f8;
}

.btn-dark {
  background-color: #7d19b6;
}

btn {
  color: #fff;
}

.close {display: none;}


.floating {
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes floating {
  0% { transform: translate(0,  0px); }
  50%  { transform: translate(0, 15px); }
  100%   { transform: translate(0, -0px); }
}

.activity{
  cursor: pointer;
}

.activity-image{
  width: 100%;
  height: auto;
}

.code-battle img {
  width: 70%;
  height: auto;
}

@media (max-width: 920px) {

  .code-battle img {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .activity-image{
    width: 110%;
  }
}

/*HORARIO*/
.horario {
  flex: 1 1 80%;
  min-height: 100vh;
  text-align: center;
}

.eventos {
  display: grid;
  grid-template-rows: repeat(30, 1fr);
  grid-template-columns: repeat(17, 1fr);
  grid-gap: 5px;
  height: 1150px;
}

.eventos-item {
  background: #46256e;
  color: #ececec;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.eventos-item.hour {
  grid-row: span 1;
  grid-column: span 2;
  padding-top: 6%;
}

.eventos-item.normal {
  grid-row: span 1;
  grid-column: span 5;
}


.eventos-item.normal-takes-2 {
  grid-row: span 2;
  grid-column: span 5;
}

.eventos-item.normal-takes-3 {
  grid-row: span 3;
  grid-column: span 5;
}

.eventos-item.normal-takes-4 {
  grid-row: span 4;
  grid-column: span 5;
}

.eventos-item.normal-takes-5 {
  grid-row: span 5;
  grid-column: span 5;
}

.eventos-item.normal-takes-7 {
  grid-row: span 7;
  grid-column: span 5;
}


.eventos-item.empty {
  opacity: 0;
}

@media (max-width: 1000px) {
  .eventos-item {
    font-size: 13px;
  }
}
